html,
body {
  position: fixed;
  overflow: hidden;
}

ons-toolbar.toolbar {
    background-image: linear-gradient(0deg,rgb(44, 58, 58),rgb(60, 102, 102) 100%)
}

.toolbar .right {
    flex: 0 0 auto;
}

.fixed-center {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.abs-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.responsive {
    float: left;
    max-width: 100%;
}

.list-item__center {
    overflow-x: hidden;
}

#feeds-page .list-item__thumbnail,
#specific-feed-page .list-item__thumbnail,
#news-page .list-item__thumbnail,
#recommended-page .list-item__thumbnail,
#add-opml-inspect-page .list-item__thumbnail,
#specific-news-item-page .list-item__thumbnail,
#bookmarks-list .list-item__thumbnail,
#favorites-list .list-item__thumbnail,
#add-specific-feed-page .list-item__thumbnail,
#storage-page .list-item__thumbnail,
.category-page-category-feeds .list-item__thumbnail,
#feeds-search-results-page .list-item__thumbnail,
#add-feed-page .list-item__thumbnail {
    width: 16px;
    height: 16px;
}

#specific-feed-page-content .list-item__subtitle img,
#add-feed-page .list-item .list-item__subtitle img,
#news-page .list-item__subtitle img {
    max-width: 100%;
}

.hidden {
    display: none;
}

.overflow-ellipsis {
    white-space: nowrap; 
    overflow: hidden;
    text-overflow: ellipsis;
}

#news-list-lazy-repeat .overflow-ellipsis {
    max-width: 100%;
    display: block;
    padding-right: 50px;
}

#bookmarks-list .overflow-ellipsis {
    max-width: 100%;
    display: block;
    padding-right: 50px;
}


#news-page-scroll-to-top {
    transition: opacity 2s ease;
}

#news-page-scroll-to-top.hidden {
    display: none;
}

#menu .ons-icon {
    color: #f5593d;
}

#splitter-side-content .page__background {
    background-color: #fff
}

ons-toolbar.toolbar {
    background-image: linear-gradient(0deg,rgba(134, 139, 145, 0.419),rgba(172, 179, 186, 0.692) 100%);
}

ons-search-input {
    width: 100%;
}

ons-search-input .search-input {
    border-radius: 0;
}

.list-item--material__left {
    min-width: 32px;
}

/* News page */

#news-page .subtitle-description p {
    margin-top: 0;
}

#news-page .subtitle-description p:last-of-type {
    margin-bottom: 0;
}

#nnews-list-lazy-repeat .list-item {
    height: 100px;
}

#about-page img {
    max-width: calc(100vw - 120px);
    border-radius: 8px;
}

.clearfix {
    height: 1px;
    clear: both;
}

.user-select-text {
    user-select: text;
}

.user-select-text * {
    user-select: text;
}

#add-feed-page-url-rss,
#add-feed-page-url-not-found-rss {
    max-width: 600px;
    width: 100%;
}

#add-opml-inspect-list-items-root .list-item h3,
#add-opml-inspect-list-items-root .list-item p {
    margin: 1px 0;
}

#add-opml-inspect-list-items-root .labeled-text {
    font-size: 14px;
    background-color: #ddd;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-left: 3px;
    padding: 0 2px;
}

#add-opml-inspect-list-items-root .icon-error,
#add-opml-inspect-list-items-root .msg-error {
    color: #f00;
}

#add-opml-inspect-list-items-root .icon-ready,
#add-opml-inspect-list-items-root .msg-ready {
    color: #00ce00;
}

#add-opml-inspect-list-items-root .icon-already-exists,
#add-opml-inspect-list-items-root .msg-already-exists {
    color: rgb(156, 67, 156);
}


/* ======================================= */
/* Feed Content Styling */
/* =======================================  */

#specific-news-item-page-enclosures .enclosure-error-msg {
    margin-top: 0;
    overflow: hidden;
}

#specific-news-item-page-enclosures .enclosure-error-msg span {
    background: #eee;
    display: inline-block;
    padding: 0 3px;
    border-left: 3px solid rgb(255, 72, 72);
    border-right: 3px solid #f00;
}

#specific-news-item-page-enclosures .enclosure-error-msg .icon {
    color: #f00;
    margin-right: 3px;
}

#specific-news-item-page-enclosures .list-item.close {
    animation: collapse 200ms ease forwards;
}

@keyframes collapse {
    to {
        height: 0;
        overflow: hidden;
    }
}

#specific-news-item-page-description img,
#specific-news-item-page-content img {
    display: block;
}

#specific-news-item-page-description > *:nth-child(1) {
    margin-top: 0;
}

/* ======================================= */
/* Icons */
/* =======================================  */

.icon-opml::after {
    content: url(static/opml.svg);
}

#specific-news-item-page-content {
    width: 100%;
    overflow-x: hidden;
}

#specific-news-item-page-description {
    width: 100%;
}

#specific-news-item-page-description img,
#specific-news-item-page-description video,
#specific-news-item-page-description audio,
#specific-news-item-page-description iframe,
#specific-news-item-page-description picture {
    max-width: 100%;
}

#specific-news-item-page-enclosures img,
#specific-news-item-page-enclosures audio,
#specific-news-item-page-enclosures video {
    max-width: 100%;
}

#specific-news-item-page-content img,
#specific-news-item-page-content video,
#specific-news-item-page-content audio,
#specific-news-item-page-content iframe,
#specific-news-item-page-content picture {
    max-width: 100%;
}

#specific-news-item-page-content code {
    overflow-x: auto;
    background-color: #1f1514;
    color: #fff;
    display: block;
    padding: 10px;
}


/* ======================================= */
/* Storage Page */
/* =======================================  */

#storage-quota {
    position: relative;
    z-index: 1;
}

#storage-quota .left{
    position: relative;
    z-index: 2;
}

#storage-quota #storage-quota-bar {
    content: "";
    position: absolute;
    display: block;
    left: 0;
    right: 100%;
    top: 0;
    bottom: 0;
    background: #f5593d;
    z-index: -1;
    transition: all 300ms ease;
    transform-origin: left center;
    animation: storageQuotaBarJumpIn 2s ease;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0);
}

@keyframes storageQuotaBarJumpIn {
    from {
        transform: scaleX(0);
    }
    to {
        transform: scaleX(1);
    }
}

#storage-quota:hover #storage-quota-bar {
    opacity: .9;
}

.background-audio-toolbar-btn {
    animation: scale 100ms ease;
    
}

#background-audio-action-sheet-audio-wrapper {
    background: #fff;
    padding: 0 10px;
}

#background-audio-action-sheet-audio-wrapper audio {
    width: 100%;
}

@keyframes scale {
    from {
        transform: scale(0);
        width: 0
    }
    to {
        transform: scale(1);
        width: 1em;
    }
}

#storage-page .ct-chart .ct-label {
    fill: #fff;
}

a.unstyled {
  text-decoration: none;
  color: inherit;
  cursor: inherit;
}



/* ======================================= */
/* Sharing */
/* =======================================  */


.resp-sharing-button__link,
.resp-sharing-button__icon {
  display: inline-block
}

.resp-sharing-button__link {
  text-decoration: none;
  color: #fff;
  margin: 0.5em
}

.resp-sharing-button {
  border-radius: 5px;
  transition: 25ms ease-out;
  padding: 0.5em 0.75em;
  font-family: Helvetica Neue,Helvetica,Arial,sans-serif
}

.resp-sharing-button__icon svg {
  width: 1em;
  height: 1em;
  margin-right: 0.4em;
  vertical-align: top
}

.resp-sharing-button--small svg {
  margin: 0;
  vertical-align: middle
}

/* Non solid icons get a stroke */
.resp-sharing-button__icon {
  stroke: #fff;
  fill: none
}

/* Solid icons get a fill */
.resp-sharing-button__icon--solid,
.resp-sharing-button__icon--solidcircle {
  fill: #fff;
  stroke: none
}

